/* Home.jsx*/

import React, { useState } from "react";

// styled-components
import styled from "styled-components";

// components
import ScoreComponent from "../components/ScoreComponent";



const Home = () => {
    //     
    const weekArr = ['', '', '', '', '', '', ''];
    //   
    const today = new Date().getDay();
    // map    index  
    const indexes = [0,1,2,3,4,5,6]
    //   - reset    state  - 
    //let [rand, setRand] = useState([Math.floor(Math.random() * 5),Math.floor(Math.random() * 5),Math.floor(Math.random() * 5),Math.floor(Math.random() * 5),Math.floor(Math.random() * 5),Math.floor(Math.random() * 5),Math.floor(Math.random() * 5)]);
    //   - map      
    let newArr = [0,0,0,0,0,0,0];
    newArr = newArr.map((value)=>(value = Math.floor(Math.random() * 5)));
    let [rand, setRand] = useState([...newArr]);

    // map  ScoreComponent   7 
    const ScoreComponents =  
        indexes.map((value,index)=>(<ScoreComponent key={index} 
                                                    day={weekArr[(today+value)%7]}
                                                    random={rand[value]}
                                                    />));

    //   random   ,
    //  7 .. rand 0~4    1  
    const sum = rand.reduce((prev, value)=> {return prev + value},7);
    //  = /7,  .toFixed(1) -   , reset html   state 
    let [aver, setAver] = useState((sum / 7).toFixed(1));
    //   
    const onReset = () =>{
        console.log("clicked")
        setAver(()=>aver=0);
        setRand(()=>rand=[]);
    }

    return (
        <>
            <Container>
                <Title> ?</Title>
                    {/*  map()    .  */}
                    {ScoreComponents}
  
                <FooterWrap>
                    <h1> </h1>
                    <h1>{aver}</h1>
                    <ResetButton onClick={onReset}>Reset</ResetButton>
                </FooterWrap>
            </Container>
        </>
    )
}

export default Home;

//  Styled Components = CSS Part

const Container = styled.div`
  max-width: 350px;
  min-height: 80vh;
  background-color: #fff;
  padding: 16px;
  margin: 20px auto;
  border-radius: 5px;
  border: 1px solid #ddd;
`;

const Title = styled.h1`
  color: slateblue;
  text-align: center;
`;

const FooterWrap = styled.div`
    text-align: center;
    color:var(--purple);
    line-height: 20px;
`;

const ResetButton = styled.button`
    padding: 20px;
    width: 200px;
    border: none;
    background-color: var(--purple);
    font-size: 20px;
    color: var(--white);
    border-radius: 10px;
`;