/* Detail.jsx*/

import { click } from "@testing-library/user-event/dist/click";
import React from "react";

// Components
import { useState, useEffect } from "react";

import "./DetailScores.Module.css"

function DetailScores() {
    // html   state .
    const [clicked, setClicked] = useState(false); // clicked    false  

    //   
    //   classname  
    //  .
    //    .
    //     class  

    function onClick(event) {
        //   id 
        setClicked((value) => value = event.target.id) //   setClicked  clicked  
    }

    //  
    const handleKeyDown = (e) => {
        //console.log(e);
        if (e.key === '1' || e.key === '2' || e.key === '3' || e.key === '4' || e.key === '5') {
            //console.log("Pressed")
            // logic
            //console.log(e.key)
            const temp = document.getElementById(e.key);
            //console.log(temp);
            //console.log("click")
            temp.classList.add("clickedScore")
        }
    }

    // useEffect() -        
    useEffect(() => {
        window.addEventListener('keydown', handleKeyDown)
        //console.log("mount")

        //      
        return () => {
            window.removeEventListener('keydown', handleKeyDown)
            //console.log("unmount")
        }
    })


    // useEffect  clicked      .
    useEffect(() => {
        ClickedCircle(); //   
    }, [clicked]);

    //  
    function ClickedCircle() {
        //   5  1,2,3,4,5 id .
        for (let i = 1; i < 6; ++i) {
            // Id     - score  1~5 .
            const temp = document.getElementById(i);
            //   setClicked  target  , 1~5  .
            console.log(clicked);

            //  
            // i      .
            //   = clicked  0 = false,  i 0 , clicked 0     .
            //  i   . -> i 1 , score  id  1 
            if (i <= clicked) {
                // i  clicked   
                //console.log(i)
                // temp   .
                temp.classList.add("clickedScore")

                //   ->    
            } else {
                if (temp.classList.contains("clickedScore")) {
                    temp.classList.remove("clickedScore")
                }
            }
        }
    };

    // map    index  
    const indexes = [1, 2, 3, 4, 5]
    const Scores = indexes.map((value, index) => (<div className="score" key={index} id={value} onClick={onClick} ></div>))

    return (
        <>
            {Scores}
        </>
    )
}

export default DetailScores;
